body {
  width: 100%;
  height: 100%;
  content: " ";
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  text-align: center;
}
html {
  font-size: 13.33333vw;
}
.activity-container.font-family {
  font-family: PingFangSC-Regular, HelveticaNeue-Light, Helvetica Neue Light,
    Microsoft YaHei, sans-serif;
}
.activity-container {
  position: relative;
  max-width: 750px;
  min-height: 100%;
  margin-left: auto;
  margin-right: auto;
  font-size: 0.28rem;
}
.banner {
  position: relative;
  display: block;
  min-height: 1rem;
  width: 100%;
}
.banner .banner-img {
  display: block;
  width: 100%;
}
.banner .banner-close {
  position: absolute;
  left: 0.24rem;
  top: 50%;
  margin-top: -0.18rem;
  display: flex;
  width: 0.36rem;
  height: 0.36rem;
  justify-content: center;
  align-items: center;
}
.banner .banner-close img {
  width: 0.24rem;
  height: 0.24rem;
}
.activity-container .activity {
  position: relative;
  z-index: 0;
}
.warp.color-1 {
  background-color: #6a8bdf;
}
.warp {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  transition: background-color 0.5s;
}
.warp .mask {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 2;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0.7)
  );
}
.card_container {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  z-index: 11;
  margin: 0 auto;
  width: 6.86rem;
  height: 8.96rem;
  perspective: 1000px;
  perspective-origin: 50% 150%;
  -webkit-perspective: 1000px;
  -webkit-perspective-origin: 50% 150%;
}
.warp.color-1 .card {
  box-shadow: 0 0.12rem 0.48rem 0 rgba(68, 79, 136, 0.24);
}
.card.draw {
  display: block;
  opacity: 1;
}
.card {
  transform-style: preserve-3d;
  width: 100%;
  height: 100%;
  border-radius: 0.24rem;
  position: absolute;
  z-index: 30;
  background: #fff;
  opacity: 0;
  display: none;
  text-align: center;
  font-size: 30px;
  line-height: 1;
}
.img-box {
  position: relative;
  z-index: 1;
  width: 6.38rem;
  min-height: 6.88rem;
  margin: -0.64rem auto 0.34rem;
  border-radius: 0.24rem;
  background: #f0f4f7;
}
.img-box img {
  margin-top: 0.2rem;
  width: 6.24rem;
  border-radius: 0.24rem;
}
img {
  border: 0;
  vertical-align: middle;
}
.warp.color-1 .alpha-box {
  background: linear-gradient(
    rgba(255, 255, 255, 0),
    rgba(106, 139, 223, 0.65)
  );
}
.img-box .alpha-box {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 2.64rem;
  border-radius: 0.24rem;
  transition: background 0.3s, opacity 0.3s;
}
.img-box .tag.show {
  opacity: 1;
}
.img-box .tag {
  position: absolute;
  z-index: 3;
  bottom: 0.2rem;
  left: 0.2rem;
  right: 0;
  font-size: 0;
  text-align: left;
  transition: opacity 0.3s;
  opacity: 0;
}
.warp.color-1 .img-box .tag span {
  color: #6a8bdf;
}
.img-box .tag span {
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.2rem;
  margin-top: 0.2rem;
  padding: 0 0.25rem;
  min-height: 0.5rem;
  line-height: 0.5rem;
  border-radius: 0.25rem;
  background: #fff;
  font-size: 0.28rem;
}
.info {
  position: relative;
  z-index: 1;
  text-align: left;
  padding: 0 0.24rem;
}
.info .text {
  margin-left: 0.2rem;
}
.info .text h6 {
  font-size: 0.36rem;
  font-weight: 500;
  margin-bottom: 0.16rem;
}
.info .text p {
  width: 4.02rem;
  font-size: 0.28rem;
  line-height: 0.44rem;
  min-height: 1.32rem;
  color: rgba(131, 138, 150, 0.66);
}
.info .read-btn {
  position: absolute;
  z-index: 2;
  top: 50%;
  right: 0.24rem;
  color: #fff;
  line-height: 0.8rem;
  height: 0.8rem;
  width: 1.8rem;
  margin-top: -0.4rem;
  border-radius: 0.4rem;
  text-align: center;
  font-size: 0;
  box-shadow: 0 0.08rem 0.24rem 0 rgba(255, 45, 45, 0.5);
  background: linear-gradient(to right, #ed424b, #ff6857);
}
em,
i,
cite {
  font-style: normal;
}
.info .read-btn i,
.info .read-btn span {
  display: inline-block;
  vertical-align: middle;
}
.info .read-btn i {
  font-size: 0.28rem;
  margin-left: 0.15rem;
  font-weight: 600;
}
em,
i,
cite {
  font-style: normal;
}
.info .read-btn span {
  width: 0.32rem;
  height: 0.32rem;
  margin: 0 0 0 0.1rem;
  background: url("data:image/svg+xml,%3Csvg height%3D%2214%22 viewBox%3D%220 0 9 14%22 width%3D%229%22 xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22 xmlns%3Axlink%3D%22http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink%22%3E%3Cdefs%3E%3Cpath id%3D%22a%22 d%3D%22m7.91925735 13.3356362 1.49481864 1.3287276 5.92387901-6.6643638-5.92387901-6.66436384-1.49481864 1.32872768 4.74278765 5.33563616z%22%2F%3E%3Cmask id%3D%22b%22 fill%3D%22%23fff%22%3E%3Cuse fill%3D%22%23fff%22 fill-rule%3D%22evenodd%22 xlink%3Ahref%3D%22%23a%22%2F%3E%3C%2Fmask%3E%3C%2Fdefs%3E%3Cg fill%3D%22%23fff%22 fill-rule%3D%22evenodd%22 transform%3D%22translate(-7 -1)%22%3E%3Cuse fill-rule%3D%22nonzero%22 xlink%3Ahref%3D%22%23a%22%2F%3E%3Cg fill-rule%3D%22evenodd%22 mask%3D%22url(%23b)%22%3E%3Cpath d%3D%22m0 0h15.238095v15.238095h-15.238095z%22%2F%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E")
    no-repeat 50% 50%;
  background-size: auto;
  background-size: contain;
}
.info .read-btn i,
.info .read-btn span {
  display: inline-block;
  vertical-align: middle;
}
.auto-height {
  height: 90%;
  display: inline-block;
  vertical-align: middle;
}
.btn_group {
  position: fixed;
  left: 0;
  right: 0;
  text-align: center;
  bottom: 0;
  font-size: 0;
  z-index: 20;
}
.btn_group a.back.disabled {
  background-image: url("data:image/png;base64,");
}
.btn_group a {
  width: 2.06rem;
  height: 1.88rem;
  display: inline-block;
  color: #fff;
  cursor: pointer;
  user-select: none;
  outline: none;
  background-repeat: no-repeat;
  background-size: 100%;
  transition: background 0.3s;
}
.btn_group a.add-fav {
  background-image: url("data:image/png;base64,");
}
